as se ajusten permanentemente en pro del mejoramiento continuo sus protocolos de actuación en atención generando eficiencia y nuevos modelos que favorezcan la pluralidad de ciudadanos en el territorio nacional. Razón por la cual la atención y el servicio al ciudadano está tomando un apoyo en las tecnologías de la información y la comunicación TIC, lo que facilita un mayor alcance y oportunidad en las respuestas.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Así mismo el programa se sustenta en la normativa actual respecto a la atención al ciudadano en las entidades públicas (NTC GP 1000), en concordancia con el Programa Nacional del Servicio al Ciudadano liderado por el Departamento Nacional de Planeación, bajo el marco del Modelo de Gestión Pública Eficiente de Servicio al Ciudadano establecido en el CONPES 3785 de 2013 y que, en uno de sus lineamientos de trabajo llamado ventanilla hacia adentro, contempla la importancia de la formación del talento humano (funcionarios públicos) en todo lo relacionado con la atención y servicio al ciudadano bajo estándares de calidad y eficiencia. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container tarjeta tarjeta__template--azul-claro p-4 p-md-5 mb-5",attrs:{"data-aos":"fade-up"}},[_c('div',{staticClass:"titulo__template--a mb-4"},[_c('span',{staticClass:"h4"},[_vm._v("2. Justificación"),_c('br'),_vm._v("del programa")])]),_c('p',[_vm._v("En 2019, la Encuesta realizada por el DANE de Cultura Política, permitió identificar un crecimiento entre el 2017 y el 2019 de los ciudadanos que acudieron a los servicios prestados por las entidades públicas pasando del 19.0 % al 25.9 % de la población colombiana, de esa población el 35 % asoció su respuesta con la necesidad de resolver un asunto y el 21.2 % con demoras en obtención de la información; lo que expresa la necesidad de las entidades públicas de contar con mayores capacidades de atención al ciudadano, así como de con